*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --dark-pink-color: #d41443;
  --light-pink-color: #ffa5ba;
  --primary-white-color: #fff;
  --product-pink-color: #ffb8ca;
  --product-creamy-color: #f0d1a5;
  --mobile-menu-bg-color: #e17992;
  --mobile-menu-border-color: #e18298;
  --product-green-color: #c2e297;
  --primary-black-color: #000;
  --text-gray-color: #907e82;
  --product-bg-color: #e5e5e5;
  --howitsmade-bg-color: #fff5f6;
  --review-bg-color: #fafafa;
  --review-element-color: #e1e1e1;
  --icon-bg-color: #fea5bb;
  --review-bg-color: #fafafa;
  --review-element-color: #e1e1e1;
  --button-bg-hover-color: #ecebeb;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --duration: 250ms;
}

.footer, .advantages__number, .about-name__another, .about-name, .about-text, .logo__text {
  font-family: Titan One, cursive;
}

html {
  box-sizing: border-box;
}

.container {
  width: 100%;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 18px;
    padding-right: 18px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

body {
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.75;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  transition: color var(--duration) var(--timing-function);
  text-decoration: none;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  height: 44px;
  min-height: 44px;
  background-color: var(--primary-white-color);
  color: var(--dark-pink-color);
  text-align: center;
  border-radius: 22px;
  font-weight: 700;
  line-height: 1.31;
}

body.modal-open {
  overflow: hidden;
}

header {
  z-index: 2;
  background-color: var(--product-pink-color);
  padding-top: 8px;
}

@media screen and (min-width: 768px) {
  header {
    padding-top: 32px;
  }
}

@media screen and (min-width: 1280px) {
  header {
    padding-top: 37px;
  }
}

.page-header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .page-header {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .page-header {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.logo__text {
  letter-spacing: .02em;
  text-transform: uppercase;
  color: var(--dark-pink-color);
  margin-left: 20px;
  font-size: 22px;
  font-weight: 400;
  line-height: 1.14;
}

@media screen and (min-width: 768px) {
