}

.mobile-menu {
  width: 100vw;
  height: 100vh;
  z-index: 2;
  background-color: var(--mobile-menu-bg-color);
  opacity: 1;
  transition: opacity 500ms var(--timing-function), transform 500ms var(--timing-function);
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(0);
}

@media screen and (min-width: 480px) {
  .mobile-menu {
    width: 250px;
  }
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    width: 410px;
  }
}

.no-scroll {
  overflow: hidden;
}

.mobile-menu.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transition: opacity 500ms var(--timing-function), transform 500ms var(--timing-function), visibility 500ms var(--timing-function);
  transform: translateX(100%);
}

.mobile-menu-container {
  height: 100%;
  max-width: 480px;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  overflow: auto;
}

.button--buy {
  width: 209px;
  transition: background-color var(--duration) var(--timing-function);
  margin: 0 auto 20px;
}

