  .button--buy {
    width: 186px;
  }
}

.button--buy:hover, .button--buy:focus {
  background-color: var(--button-bg-hover-color);
}

.button--tablet {
  transition: background-color var(--duration) var(--timing-function);
  display: none;
}

@media screen and (min-width: 768px) {
  .button--tablet {
    width: 160px;
    margin-left: 32px;
    display: block;
  }
}

@media screen and (min-width: 1280px) {
  .button--tablet {
    margin-left: 0;
  }
}

.button--tablet:hover, .button--tablet:focus {
  background-color: var(--button-bg-hover-color);
}

.button__icon {
  --color1: var(--dark-pink-color);
}

.button__text {
  margin-right: 15px;
}

.button__text--tablet {
  margin-right: 8px;
}

.menu-open-btn, .menu-close-btn {
  background-color: rgba(0, 0, 0, 0);
  padding: 0;
  display: block;
}

@media screen and (min-width: 1280px) {
  .menu-open-btn {
    display: none;
  }
}

.menu-close-btn {
  margin: 20px 20px 26px auto;
}

.icon-menu {
  stroke: var(--dark-pink-color);
  transition: stroke var(--duration) var(--timing-function);
}

.icon-menu:hover, .icon-menu:focus {
  stroke: var(--primary-white-color);
}

.icon-cross {
  stroke: var(--primary-white-color);
  transition: stroke var(--duration) var(--timing-function);
}

.icon-cross:hover, .icon-cross:focus {
  stroke: var(--dark-pink-color);
}

.menu-nav__item {
  border: solid var(--mobile-menu-border-color);
  border-width: 1px 0 0;
  justify-content: start;
  align-items: center;
  display: flex;
}

.menu-nav__item:last-child {
  border: solid var(--mobile-menu-border-color);
  border-width: 1px 0;
}

.menu-nav__link {
  color: var(--primary-white-color);
  transition: color var(--duration) var(--timing-function);
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
}

.menu-nav__link:hover, .menu-nav__link:focus, .menu-nav__link--current {
  color: var(--dark-pink-color);
}

.mobile-menu__logo {
  background-color: var(--product-pink-color);
  border: 4px solid var(--light-pink-color);
  width: 210px;
  height: 57px;
  border-radius: 4px;
  margin: auto 20px 50px auto;
}

.logo-menu {
  animation: rotateY 3s linear infinite;
}

@keyframes rotateY {
  0% {
    transform: perspective(400px)rotateY(0deg);
  }

  100% {
    transform: perspective(400px)rotateY(360deg);
  }
}

